body {
  color: #000;
  background-color: #fff;
  margin: 0;
  font-family: Arial, sans-serif;
}

.people {
  gap: 40px;
  max-width: 1500px;
  margin: 0 auto;
  padding: 60px 16px;
  display: grid;
}

@media (width >= 500px) {
  .people {
    grid-template-columns: repeat(2, 1fr);
    padding: 30px 35px;
  }
}

@media (width >= 750px) {
  .people {
    grid-template-columns: repeat(2, 1fr);
    padding: 30px 160px;
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
    padding: 30px 55px;
  }
}

@media (width >= 1500px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
    padding: 30px 305px;
  }
}

.img {
  border-radius: 100px;
  width: 200px;
  height: 200px;
  margin: 0 auto 12px;
  display: block;
}

.person {
  text-align: center;
  position: relative;
}

.person .details {
  position: relative;
}

.person .extra {
  opacity: 0;
  pointer-events: none;
  background-color: #f5f5f5;
  border-radius: 6px;
  gap: 4px;
  padding: 4px 8px;
  font-size: 14px;
  transition: opacity .3s 1s, transform .3s 1s;
  display: flex;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%)translateY(0);
  box-shadow: 0 2px 4px #0000001a;
}

.person:hover .extra {
  opacity: 1;
  transform: translateX(-50%)translateY(8px);
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
}

.sport {
  color: #000;
  font-size: 18px;
  font-weight: 400;
}

.sport:after {
  content: ",";
}

.pay:before {
  content: "$";
}

.pay:after {
  content: "M";
}
